@import url(https://fonts.googleapis.com/css2?family=Alex+Brush&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
    --primary-color: #F9CA00;
    --secondary-color: #E77300;

    --primary-bg-color: #F2F2F2;
    --secondary-bg-color: #e7740081;

    --white: #fcfcfc;
    --black: #0a0101;
}
.o-header{
    background-color: var(--primary-color);
    padding: 1.5rem;
}

.a-site-title {
    color: var(--secondary-color);
    font-family: 'Alex Brush';
    font-size: 37pt;
    color: var(--white);
    text-align: center;
}

.m-menu-links {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 0;
    margin: 0;
    color: var(--secondary-color);
}

.a-menu-action {
    color: var(--white);
    background-color: var(--secondary-color);
    padding: 3px 15px;
    border-radius: 16px;
}


/* form */

.a-orderBtn {
    background-color: var(--secondary-color) !important;
    border: none !important;
}

.m-confirmation {
    display: flex;
    justify-content: center;
    align-items: center;
}

.a-order-confirmation {
    text-align: center;
}
/* SHOPPING CART */
.o-cart {
    background-color: var(--secondary-color);
    width: 100%;
    padding: 1rem;
    border-radius: 5px 5px 0px 0px;
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    color: var(--white);
    z-index: 2000;

}

.m-cart {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
}
.a-main-nextBtn {
    background-color: var(--primary-color);
    border: none;
    border-radius: 19px;
    padding: 5px 27px;
    color: var(--white);
}

.a-main-priceBtn {
    background-color: var(--white);
    border: none;
    border-radius: 19px;
    border: 1px solid var(--primary-color);
    padding: 5px 27px;
    color: var(--secondary-color);
}

/* Layover */
.o-layover {
    z-index: 1000;
    height: 100%;
    position: relative;
    width: 100%;
    background-color: var(--secondary-bg-color);
}

.o-cart-title {
    text-align: left;
    padding-left: 1rem;
}


.o-main-content {
    background-color: var(--primary-bg-color);

}
.o-main {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    z-index: 1;
    position: relative;

}

.m-main {
    width: 95vw;
    height: 85vh;
    padding: 0rem 2rem;
    border-radius: 9px;
    background-color: var(--white);
    overflow-y:auto
}
.m-menu-items {
    display: flex;
    justify-content: space-evenly;
    list-style: none;
    padding: 2rem;
    color: var(--secondary-color);
    border-bottom: 1px solid var(--primary-color);
    font-weight: bold;
    font-size: 14pt;
}
/* CARD */
.m-menuItem-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    border: 2px solid var(--secondary-color);
    border-radius:  19px;
    width: 100%;

}
.o-menuItem-card {

    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2rem;
}
.a-menuItem-picture {
    width: 100%;
    height: 180px;
    object-fit: cover;
    padding: 5px;
    border-bottom: 1px solid var(--primary-color);
}
.a-menuItem-title {
    text-align: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.a-menuItem-action {
    display: block;
    width: 95%;
    background-color: var(--secondary-color);
    color: white;
    border-radius: 19px;
    padding: 0.3rem;
    margin-bottom: 1rem;
    text-align: center;
    font-weight: bold;
}

.m-menuItem-price {
    padding-top: 1rem;

    /* position: relative; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.a-menuItem-price {
    font-weight: bold;
    color: var(--secondary-color);
    font-size: 14pt;
}




